@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,200;0,300;0,400;0,500;1,100&display=swap);
html, body{
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgb(230, 230, 230);
}
*{
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}
h1{
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  width: 100vw;
  justify-content: space-evenly;
  grid-gap: 50px;
  gap: 50px;
}

.edit-information{
  max-width: 40vw;
  min-width: 40vw;
  min-height: 80vh;
  border-radius: 5px;
  box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.096);
  background-color: #e2ece9;
}
.header{
  min-width: 100vw;
  min-height: 15vh;
  background-color: #264653;
  display: flex;
  align-items: center;
}
.header h1{
  font-family: 'Roboto', sans-serif;
  color: white;
  padding: 25px;
  font-size: 35px;
  margin-left: 150px;
}
.title-section{
  font-family: 'Roboto', sans-serif;
  color:rgba(0, 0, 0, 0.705)
}
.content{
  display:flex;
  flex-direction: row;
  justify-content: center;
  grid-gap: 100px;
  gap: 100px;
}
.main-container{
  max-width: 40%;
  max-height: 80vh;
  min-height: 80vh;
  border-radius: 5px;
  box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.096);
  background-color: #e2ece9;
}
.preview{
  min-width: 40%;
  min-height: 80vh;
  max-height: 100vh;
  box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.096);
  background-color: white;
  display: flex;
  flex-direction: column;
  position: -webkit-sticky;
  position: sticky; 
  top: 10px;
  margin-bottom: 10px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.inputs{
  margin: 10px;
}
input{
  margin: 5px;
  padding: 10px;
  width: 80%;
  font-family: 'Roboto', sans-serif;
  font-size: 13px;
  resize: none;
  border: 1px solid rgb(209, 209, 209)
}
input:hover{
  transition: border-color 0.3s ease-in-out;
  border: 1px solid rgb(136, 136, 136);
}
input:focus{
  outline: none;
  border: 1px solid rgb(34, 34, 34);
}

button{
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  background-color: #264653;
  width: 10%;
  height: 30px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s ease 0s;
  animation-fill-mode: none;
  color: white;
  margin-top: 10px;
}
button:hover {
  color: rgb(110, 110, 110);
  transform: translateY(-2px);
}
.buttons-section{
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
  background-color: #264653;
  margin: 50px 0 25px 0;
  grid-gap: 10px;
  gap: 10px;
}
.buttons-section button{
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  background-color: #53899e;
  width: 25%;
  height: 50px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s ease 0s;
  animation-fill-mode: none;
  color: white;
  margin-top: 10px;
  margin: 10px;
}
.preview-content{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}
.header-preview{
    width: 100%;
    min-height: 15%;
    max-height: 15%;
    background-color: #14213dde;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    overflow: hidden;
    text-overflow: ellipsis;
    grid-gap: 5px;
    gap: 5px;
}
.name-preview{
    display: flex;
    flex-direction: row;
    margin-left: 50px;
    height: 50%;
}
.name-preview h3{
    color: white;
    font-size: 27px;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    margin:15px 0 0 0;
}
.job-preview{
    margin-left: 50px;
}
.job-preview h3{
    color: white;
    font-size: 23px;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    color: #118ab2;
    margin: 0;
}
.second-header-preview{
    width: 100%;
    height: 10%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: #14213df3;
}
.shc-preview{
    display: flex;
    flex-direction: row;
    width: 50%;
    height: 50%;
}
.shcc-preview{
    margin-left: 30px;
    display: flex;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-size: 15px;
    font-weight: 100;
}
i{
    color: white;
    font-size: 15px;
}

.subtitle-preview{
    color: red;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    margin: 15px 0 0 35px;
    font-family: 'Roboto', sans-serif;
    color:rgb(43, 43, 43);
}
.subtitle-preview hr{
    width: 70%;
    height: 2%;
    margin: 5%;
    border-radius: 5px;
}
.experience-container-preview{
    display:flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
    height: 60px;
    margin-bottom: 25px;
}
.experience-container-preview h3{
    color: rgb(43, 43, 43);
    font-family: 'Roboto', sans-serif;
}
.experience-detail{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40%;
    height: 50%;
    font-family: 'Roboto', sans-serif;
}

